@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  scroll-behavior: smooth;
  font-family: 'Montserrat',sans-serif;
  background-color: #FFFFFF!important;
  overflow-x: hidden;
}

.App {
  max-width: 100vw;
  max-height: 100vh;
  /*overflow-x: hidden;*/
  /*overflow-y: scroll;*/
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.main-sections{
  line-height: 2.25em;
  width: 98%;
  margin: 0 auto 5%;
}

.container-user-details{
  padding: 1%;
}

.container-sub-headers{
  font-size: 2em;
  font-weight: bold;
  margin-bottom: 50px;
}

.container-description{
  padding-bottom: 10px;
  font-size: 18px;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@-webkit-keyframes pulse {
  0%,
  to {
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=40)";
    filter: alpha(opacity=40);
    opacity: .4;
    top: 0
  }
  50% {
    -ms-filter: none;
    -webkit-filter: none;
    filter: none;
    opacity: 1;
    top: -10px
  }
}

@keyframes pulse {
  0%,
  to {
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=40)";
    filter: alpha(opacity=40);
    opacity: .4;
    top: 0
  }
  50% {
    -ms-filter: none;
    -webkit-filter: none;
    filter: none;
    opacity: 1;
    top: -10px
  }
}

@media only screen and (min-width: 1170px){
  .main-sections{
    width: 70%;
  }
}

.topbar{
    position: fixed;
    left:0;
    top:0;
    width:100%;
    text-align:center;
    box-shadow: 0 4px 4px rgba(0,0,0,0.2);
    transition:top .3s;
    z-index: 100;
    font-size: 10px;
}

@media only screen and (min-width: 1170px){
    .topbar{
        font-size: 14px;
    }
}

.hide{
    top:-57px
}

.tech-icon-row{
    margin: 20px 0;
}

.tech-icons{
    height: 110px;
    font-size: 85px;
    padding: 15px;
}

.tech-icons img{
    height: 110px;
    font-size: 85px;
    padding: 15px;
}

.timeline{
    padding: 2%;
}

.resume-timeline {
    width: 100% !important;
    max-width: unset !important;
    margin-top: 30px;
}

.vertical-timeline-element-content{
    background: rgb(46, 86, 136);
    color: #fff;
}

.vertical-timeline-element-icon{
    background: rgb(46, 86, 136);
    box-shadow: 0 0 0 0 #333333, inset 0 2px 0 rgba(0,0,0,.08), 0 3px 0 4px rgba(0,0,0,.05);
    color: #fff;
}

.vertical-timeline-element-content-arrow{
    border-right: 7px solid rgb(46, 86, 136);
}

.vertical-timeline-element-subtitle{
    padding-bottom: 10px;
    font-size: 14px;
    border-bottom: solid lightgrey;
}

.vertical-timeline-element-title{
    padding-bottom: 10px;
    font-size: 18px;
    font-weight: 600;
}

.vertical-timeline-bullet-container{
    text-align: left;
}

.resume-timeline::before {
    background: #333A40;
}

.timeline-bullet-point{
    font-size: 14px !important;
}

.vertical-timeline-element-date{
    color: white;
    font-size: 15px !important;
}

@media only screen and (min-width: 1170px){
    .vertical-timeline-element-date{
        color: #333333;
        font-size: 17px !important;
        text-align: left;
    }
}

.main-accordion{
    line-height: 24px;
}

.card{
    text-align: left;
}

.project-name{
    font-size: 18px;
    font-weight: bold;
    margin: 0;
}

.project-brief-description{
    font-size: 13px;
    font-weight: bold;
}

.project-technologies-used{
    font-size: 14px;
    font-weight: lighter;
}

.company-name{
    font-style: italic;
    font-weight: lighter;
}

.toggle-button{
    color: #3D434C;
    background-color: #CCE1FF;
    font-size: 14px;
}

